.app-wrapper {
  margin: 0 auto;
  min-width: 1855px;
  min-height: 1080px;
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
  grid-template-areas: 
  "h h"
  "n c";
}
.app-wrapper-content {

  grid-area: c;
  background-color: #aed785;

}


